/* ************** SCROLL DOWN **************** */

.scroll_down {

    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ********************MEDIA QURIES (MEDIUM DEVICES)********************** */

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }

}

/* ********************MEDIA QURIES (SMALL DEVICES)********************** */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header_container h1 {
        font-size: 25px;
    }

    .header_socials,.scroll_down {
        display: none;
    }
}

